// Menu navbar

const menuToggler = () => {
    const menuChange = document.querySelector('.menu-toggler .menu-toggler--open');
    const links = document.querySelector('.links');
    const nav = document.querySelector('.navbar-nav');
    const removeLogo = document.querySelector('.logo-header');
    const addWidth = document.querySelector('.logo');




    menuChange.addEventListener('click', () =>{
        
        
        links.classList.toggle('links--remove');
        nav.classList.toggle('navbar--revert_bg');
        removeLogo.classList.toggle('logo-header--remove');
        addWidth.classList.toggle('logo--width');
    });


    
}

menuToggler();

